import { postData } from "../services/requests";

const forms = (state) => {
    const form = document.querySelectorAll('form'),
          inputs = document.querySelectorAll('input'),
          upload = document.querySelectorAll('[name="upload"]');

    const message = {
        loading: 'Загрузка...',
        success: 'Спасибо! Скоро мы с вами свяжимся',
        failure: 'Что-то пошло не так...',
        spinner: 'assets/img/spinner.gif',
        ok: 'assets/img/ok.png',
        fail: 'assets/img/fail.png'
    };

    const path = {
        designer: 'assets/server.php',
        question: 'assets/question.php'
    };

    const clearInputs = () => {
        inputs.forEach(item => {
            item.value = '';
        });
        upload.forEach(item => {
            item.previousElementSibling.textContent = "Файл не выбран";
        });
    };

    upload.forEach(item => {
        item.addEventListener('input', () => {
            // обращаемся к первому файлку который был туда загружен
            console.log(item.files[0]);
            let dots;
            const arr = item.files[0].name.split('.');
            arr[0].length > 6 ? dots = "..." : dots = '.';
            // // 'afawefwefowakfewfe.jpg' => ['redsfaadw', 'jpg']
            // // вместо этого 
            // // item.files[0].name.split('.')[0].length > 6 ? dots = "..." : dots = '.';
            const name = arr[0].substring(0, 6) + dots + arr[1];
            item.previousElementSibling.textContent = name;
        });
    });

    form.forEach(item => {
        item.addEventListener('submit', (e) => {
            e.preventDefault();

            let statusMessage = document.createElement('div');
            // в статусе выстроить по центру можно еще добавить 
            statusMessage.classList.add('status');
            item.parentNode.appendChild(statusMessage);
            // item.insertAdjacentElement('afterend', statusMessage);

            // анимация скрытия вверх
            item.classList.add('animated', 'fadeOutUp');
            setTimeout(() => {
                item.style.display = 'none';
            }, 400);

            let statusImg = document.createElement('img');
            statusImg.setAttribute('src', message.spinner);
            // statusImg.src = message.spinner;
            // анимация появления снизу
            statusImg.classList.add('animated', 'fadeInUp');
            statusMessage.appendChild(statusImg);

            const textMessage = document.createElement('div');
            textMessage.textContent = message.loading;
            statusMessage.appendChild(textMessage);

            const formData = new FormData(item);
            // Д.З калькулятор БД
            if (item.getAttribute('data-calc') === "calc") {
                for (let key in state) {
                    formData.append(key, state[key]);
                }
            }


            // нужна для того чтобы сформировать динамический путь куда мы будем все это отправлять
            let api;
            // если этот блок будет действительно существовать у каких там там родителей то тогда мы получим этот блок
            // если этого блока просто не будет его наш скрипт не найдет то мы получим false по факту null
            //  если у формы есть родитель этот класс то все ок
            item.closest('.popup-design') || item.classList.contains('calc_form') ? api = path.designer : api = path.question;
            console.log(api);


            postData(api, formData)
                .then(res => {
                    console.log(res);
                    statusImg.setAttribute('src', message.ok);
                    textMessage.textContent = message.success;
                })
                .catch(() => {
                    statusImg.setAttribute('src', message.fail);
                    textMessage.textContent = message.failure;
                })
                .finally(() => {
                    clearInputs();
                    for (let key in state) {
                        delete state[key];
                    }
                    setTimeout(() => {
                        statusMessage.remove();
                        // стосит none
                        item.style.display = 'block';
                        // скрытие формы
                        item.classList.remove('fadeOutUp');
                        // красивое появление формы
                        item.classList.add('fadeInUp');
                    }, 5000);
                });
        });
    });

};

export default forms;